body {
    margin: 0;
}

.root {
    user-select: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
    position: absolute;
    top: 0;
}
.root > *{
    z-index: 101;
    padding: 10px;
    background: rgba(67, 215, 158, 0.15);
    color: white;
    border-radius: 10px;
}
.chart-block {
    display: flex;
    gap: 5px;
    max-width: 100vw;
    height: 250px
}
.description {
    font-size: 20px;
}

.forms {
    font-size: 15px;
}

.forms .buttons {
    display: flex;
    gap: 7px;
}

.forms button {
    padding: 5px;

}

input, button, select {
    font-size: inherit;
}